.app-wrapper{
    display: grid;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 100px 1fr;
    grid-auto-columns: 2fr 10fr;
    width: 1200px;
    margin: 0 auto;
    gap: 10px;
}
.app-wrapper-content{
    grid-area: c;
    background-color: whitesmoke;
}






