.wrapper_user {
    display: flex;
    margin-bottom: 10px;
    margin-top: 10px;
}

.avatar img {
    display: block;
    width: 100px;
    height: 100px;
}

.avatar_button{
    display: flex;
    flex-direction: column;
}

.button_wrapper{
    text-align: center;
}

.description{
    border: solid 1px black;
    border-radius: 10px;
    width: 80%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-content: space-between;
    margin-left: 10px;
}

.description div:nth-child(2){
    text-align: right;
}

.description div {
    padding: 10px;
}

.spanPageNumber{
    color: black;
    font-size: 20px;
    margin: 0 5px;
}

.selectedPage{
    font-weight: bold;
    color: #1ea7fd;
    font-size: 20px;
}

.spanPagesName{
    font-size: 20px;
}

.spanPageNumber:hover{
    cursor: pointer;
    font-weight: bold;
    color: #1ea7fd;
}